.calendar-page {
    margin-top: 0;

    &__title {
        @media (max-width:$desktop) {
            padding: 0 16px;
        }
    }

    &__container {
        @media (max-width:$desktop) {
            padding: calc(var(--header-height) + 24px) 0 16px;
            background-color: var(--stand-bg);
        }
    }

    &__body {
        padding: 32px;
        background-color: var(--stand-bg);
        border-radius: 16px;

        @media (max-width:$desktop) {
            margin-top: 24px;
            background-color: var(--white);
            padding: 0;
        }
    }

    &__calendar {
        display: grid;
        grid-template-columns: 1fr 350px;
        grid-column-gap: 16px;

        @media (max-width:$desktop) {
            box-shadow: none;
            padding: 16px;
        }
    }

    .calendar-primary {
        background-color: var(--white);
        padding: 24px;
        border-radius: 12px;
        box-shadow: var(--shadow-primary);
    }

    &__info {
        height: 100%;

        .fc-event-big {
            &:not(:last-child) {
                border-bottom: 1px solid var(--primary100);
                margin-bottom: 16px;
                padding-bottom: 16px;
            }
        }
    }

    &__new-event {
        margin-left: auto;
        color: var(--blue);

        display: flex;
        align-items: center;
        gap: 6px;

        svg {
            fill: currentColor;
            width: 12px;
            height: 12px;
        }
    }
}

.calendar-info {
    background-color: var(--white);
    padding: 24px;
    border-radius: 12px;
    box-shadow: var(--shadow-primary);
    min-height: 100%;

    &__header {}

    &__content {
        max-height: 569px;

        .simplebar-track.simplebar-vertical {
            margin-right: -16px;
        }
    }

    @media (min-width:$desktop-min) {
        &._sticky {
            position: sticky;
            top: 16px;
            width: 100%;
        }
    }
    &._one-style{
        .fc-event-big{
            &::before{
                height: 100%;
            }
        }
    }
}
